* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country_element {
display: flex;
align-items: center;
}
.country_name {
  font-size: 22px;
  padding-left: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.country_name_one {
  font-size: 38px;
  padding-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: bold;
}